.item-portrait {
  width: 75px;
  height: 75px;
}

.item-portrait:not(:first-child) {
  margin-left: 5px;
}

.item-container {
  display: flex;
  align-items: center;
  margin-left: 30px;
}

tbody tr {
  border : 1px solid #000;
  border-bottom: 5px solid red;
}

@media screen and (max-width: 768px) {
  .item-portrait {
    width: 32px;
    height: 32px;
  }
}
